<template>
  <div class="col-xs-12 col-sm-12 col-md-9">
    <div class="content">
      <header>
        <h2>Manage User</h2>
      </header>
      <transition name="slide-fade">
        <div v-if="!details">
          <b-col sm="6" class="my-1">
            <b-form-group>
              <b-input-group size="sm">
                <b-form-input
                  type="search"
                  id="filterInput"
                  v-on:keyup.enter="fetchData()"
                  v-model="term"
                  placeholder="Type to Search"
                ></b-form-input>
                <b-input-group-append>
                  <b-button v-b-tooltip.hover @click="fetchData()" title="Cari">
                    <i class="fas fa-search"></i>
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
          </b-col>
          <div class="col-sm-12">
            <b-table
              show-empty
              :items="items"
              :fields="fields"
              :busy="isBusy"
              :current-page="currentPage"
              :per-page="0"
            >
              <template v-slot:table-busy>
                <div class="text-center text-danger my-2">
                  <b-spinner class="align-middle"></b-spinner>
                  <strong>Loading...</strong>
                </div>
              </template>

              <template v-slot:cell(actions)="row">
                <b-button
                  size="sm"
                  class="mr-1"
                  v-b-tooltip.hover
                  title="Edit"
                  @click="editData(row.item)"
                >
                  <i class="far fa-edit"></i>
                </b-button>
                <b-button
                  size="sm"
                  class="mr-1"
                  v-b-tooltip.hover
                  title="Remove"
                  @click="removeData(row.item)"
                >
                  <i class="far fa-trash-alt"></i>
                </b-button>
              </template>
            </b-table>
            <b-pagination
              align="center"
              v-model="currentPage"
              :total-rows="totalItems"
              :per-page="perPage"
              size="sm"
              aria-controls="my-table"
            ></b-pagination>
          </div>

          <div class="button-group">
            <a class="button button-next" @click="details = true">
              <i class="fas fa-plus"></i> Tambah User
            </a>
          </div>
        </div>
      </transition>

      <transition name="details">
        <div v-if="details">
          <form @submit.prevent="savedData()">
            <div class="form-group">
              <div class="fieldset">
                <label>Username</label>
                <b-form-input v-model="data.username" :type="text"></b-form-input>
              </div>
              <div class="fieldset">
                <label>Email</label>
                <b-form-input v-model="data.email" :type="text"></b-form-input>
              </div>
              <div class="fieldset">
                <label>No. Handphone</label>
                <b-form-input v-model="data.phoneNumber" :type="text"></b-form-input>
              </div>
              <div class="fieldset">
                <label>Hak Akses</label>
                  <multiselect
                    v-model="selectedRoles"
                    id="ajax"
                    label="rolename"
                    track-by="rolename"
                    placeholder="Type to search"
                    open-direction="bottom"
                    :options="roles"
                    :multiple="true"
                    :searchable="true"
                    :loading="isLoading"
                    :internal-search="false"
                    :clear-on-select="true"
                    :close-on-select="false"
                    :options-limit="300"
                    :max-height="150"
                    :show-no-results="false"
                    :hide-selected="true"
                    @search-change="asyncFind"
                  >
                    <template slot="tag" slot-scope="{ option, remove }">
                      <span class="custom__tag">
                        <b-badge  variant="secondary">
                          {{ option.rolename }}
                        <i class="fas fa-times" @click="remove(option)"></i>
                        </b-badge>
                      </span>
                    </template>
                    <template slot="clear" slot-scope="props">
                      <div
                        class="multiselect__clear"
                        v-if="selectedRoles.length"
                        @mousedown.prevent.stop="clearAll(props.search)"
                      ></div>
                    </template>
                    <span
                      slot="noResult"
                    >Oops! No elements found. Consider changing the search query.</span>
                  </multiselect>
              </div>
              <div class="fieldset">
                <label></label>
                <b-form-checkbox v-model="data.actived" name="check-button" switch>Aktif</b-form-checkbox>
              </div>
              <div class="fieldset">
                <label></label>
                <b-form-checkbox v-model="data.alowLogin" name="check-button" switch>Allow Login</b-form-checkbox>
              </div>
            </div>

            <hr />

            <div class="button-group">
              <button type="button" class="button-back" v-on:click="resetField()">Kembali</button>
              <button type="submit" class="button-next">Simpan</button>
            </div>
          </form>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
import userService from "../service/user-service";
import roleService from "../service/role-service";
import { isNullOrUndefined } from "util";
import Multiselect from "vue-multiselect";
import "vue-multiselect/dist/vue-multiselect.min.css";

export default {
  name: "user",
  components: {
    Multiselect
  },
  data() {
    return {
      items: [],
      fields: [
        {
          key: "username",
          label: "Username",
          sortable: true
        },
        {
          key: "email",
          label: "Email",
          sortable: true
        },
        {
          key: "phoneNumber",
          label: "Phone Number",
          sortable: true
        },
        { key: "actions", label: "Actions", class: "text-center" }
      ],
      currentPage: 1,
      perPage: 5,
      totalItems: 0,
      details: false,
      term: "",
      isBusy: false,
      data: {},
      selectedRoles: [],
      roles: [],
      isLoading: false
    };
  },
  mounted() {
    this.fetchData().catch(error => {
      window.console.error(error);
    });
  },
  methods: {
    async fetchData() {
      this.isBusy = true;
      return new Promise((resolve, reject) => {
        userService
          .getUser(this.currentPage, this.perPage, this.term)
          .then(response => {
            this.totalItems = response.data.count;
            this.items = response.data.data;
            this.isBusy = false;
            resolve(response);
          })
          .catch(() => {
            reject("error");
          });
      });
    },
    savedData() {
      return new Promise((resolve, reject) => {
        userService
          .createUser(this.data)
          .then(response => {
            this.$notify({
              group: "notif",
              type: "success",
              title: "Info: ",
              text: response.data.message
            });
            this.resetField();
            resolve(response);
          })
          .catch(err => {
            reject(err);
          });
      });
    },
    editData(data) {
      this.data = data;
      this.details = true;
    },
    removeData(data) {
      if (
        confirm("Apakah anda ingin menghapus user '" + data.username + "' ?")
      ) {
        return new Promise((resolve, reject) => {
          userService
            .removeUser(data)
            .then(response => {
              this.$notify({
                group: "notif",
                type: "success",
                title: "Info: ",
                text: response.data.message
              });
              this.fetchData().catch(() => {});
              resolve(response);
            })
            .catch(err => {
              reject(err);
            });
        });
      }
    },
    resetField() {
      this.fetchData();
      this.data = {};
      this.details = !this.details;
    },
    limitText(count) {
      return `and ${count} other roles`;
    },
    asyncFind(query) {
      this.isLoading = true;
      return new Promise((resolve, reject) => {
        roleService
          .getRoles(query)
          .then(response => {
            this.roles = response.data.data;
            this.isLoading = false;
            resolve(response);
          })
          .catch(() => {
            reject("error");
          });
      });
    },
    clearAll() {
      this.selectedRoles = [];
    }
  },
  watch: {
    currentPage: {
      handler: function(value) {
        if (!isNullOrUndefined(value)) {
          this.fetchData().catch(() => {});
        }
      }
    }
  },
  created() {
    this.$store.dispatch('SessionLogin')
        .then(result => {
          if (result == 'session_habis') {
            this.$notify({
              group: 'notif',
              title: 'Session Login Expired',
              text:  'Plese Login Again',
              type:  'warn',
              duration: 5000
            });
            this.$store.dispatch('logout');
          }
    });
  }
};
</script>
<style>
.slide-fade-enter-active {
  transition: all 0.1s ease;
}
.slide-fade-leave-active {
  transition: all 0.1s ease;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
.badge-secondary {
    color: #fff  !important;
    background-color: #1487D6  !important;
    font-size: 10px !important;
    padding: 2px 12px  !important;
    margin-bottom: 8px  !important;
    margin-right: 4px  !important;
}
.multiselect {
    width: calc(50% - 32px) !important;
}
.custom__tag i {
    margin-left: 4px;
}
.multiselect__strong {  
    display: block !important;
    font-size: 11px !important;
}
.custom-control-label {
    width: auto !important;
    line-height: 1.75;
}
@media only screen and (max-width: 767px) {
  table {
    display: inline-table !important;
  }
}
</style>