<template>
<body>
  <div class="container" style="margin: auto;">
    <nav class="navigation">
      <div class="nav-toggle">
        <i class="fas fa-bars"></i>
      </div>
      <div class="nav-brand">
        <img src="../assets/img/pac-logo.png" />
        <span>Web Admin PAC</span>
      </div>
    </nav>
  </div>

  <main>
    <div class="container">
      <div class="login-form">
        <div class="left">
          <img src="../assets/img/pac-logo.png" />
        </div>
        <notifications group="auth" position="top center"/>
        <form @submit.prevent="callLogin()">
          <div class="form-group">
            <div class="fieldset">
              <label>User ID</label>
              <input type="text" v-model="user" placeholder="Username" autofocus />
            </div>
            <div class="fieldset">
              <label>Password</label>
              <input type="password" placeholder="password" v-model="password" />
            </div>
            <div class="fieldset">
              <b-btn variant="success" type="submit" :disabled="loading">
                <b-spinner small v-if="loading"></b-spinner>
                <span class="sr-only" v-if="loading">Loading...</span>
                <span v-if="!loading">Log in</span>
              </b-btn>
            </div>
          </div>
        </form>
      </div>
    </div>
  </main>
</body>
</template>
   

<script>
import "select2/dist/js/select2";
import "jquery/dist/jquery";
import "jquery-ui/ui/jquery-1-7";
import "slick-carousel/slick/slick";
import "../assets/js/script";
import "../assets/css/sanitize.css";
import "../assets/css/all.css";
import keamanan from '../service/enkripsi'
import cek from '../service/cekToken'
import router from '../router'
// import { isNullOrUndefined } from "util";

export default {
  name: "login",

  data() {
    return {
      loginError: false,
      user: "",
      password: "",
      error: false,
      errors: [],
      loading: false,
    };
  },
  methods: {
    callLogin: function() {
      this.loading = true;
      this.errors = [];
      
      // this.$store.dispatch("login", { user: this.user, password: this.password })
      //   .then(
      //     () => {
      //       this.loading = false;
      //       this.$router.push("/");
            
      //     },
      //     response => {
      //       this.loading = false;
      //       this.$notify({
      //         group: "notif",
      //         type: "error",
      //         title: "Error: ",
      //         text: response
      //       });
      //     }
      //   )
      //   .catch(error => {
          
      //     if (isNullOrUndefined(error.response.data)) {
      //       this.loading = false;
            
      //       this.$notify({
      //         group: "notif",
      //         type: "warn",
      //         title: "Info: ",
      //         text: error.response.data.message
      //       });
      //     }

      //     this.loginError = true;
      //     this.errors.push(error);
      //     this.error = true;
          
      //   });
      
      this.$store.dispatch("login", { 
        user:      this.user,
        password:  this.password
      }).then(data => {
        if(data.code == "00") {
          setTimeout(() => {
            this.loading     =    false;
          }, 2000);
          this.$notify({
            group: "auth",
            type:  "success",
            title: "Authentication Success",
          });
          let token      =        data.data[0].token;
          let encrypted  =        keamanan.enkripsi(token);
          this.$store.dispatch("loginService", { token: encrypted });
        } else {
          this.loading   =    false;
            this.$notify({
              group: "auth",
              type: "error",
              title: "Authentication Failed ",
              text: data.message,
              duration: 5000
            });   
            // window.console.log(data);      
        }
        // window.console.log(data);
      }).catch(pusing => {
        this.loading         =    false;
        window.console.log(pusing);
      });
    }
  },
  created() {
    let cekToken     =    cek.CekToken();
    if (cekToken == null) {
      return
    } else {
      router.replace('/');
    }
  }
};
</script>
<style lang="scss" scoped>
  body {
    background: #f5f5f5;
    padding-bottom: 50px;
    position: relative;
    margin: 0;
    min-height: 100vh;
  }
  .navigation {
    width: 100%;
    left: 0;
    /* margin-left: 8px; */
    margin: 0;
    border-radius: 0;
    .nav-toggle {
      display: none;
    }
  }
  .vue-notification-group {
    top: 396px !important;
  }
  .fieldset {
    label {
      width: 100% !important;
    }
  }
  .login-form {
    .left {
      img {
          width: 80%;
          position: relative;
          margin-top: 41px;
          margin-left: 17px;
      }
    }
    form {
          margin-right: 20px;
    }
  }
  
  // this media screen mobile 
  @media only screen and(max-width: 990px) {
    .login-form {
      height: 492px;
      width: 51%;
      form {
        position: absolute;
        padding-top: 94px;
        margin: auto;
        left: 0;
        right: 0;
        width: 347px;
      }
      .left {
        left: 0;
        right: 0;
        text-align: center;
        position: relative;
        width: 100%;
        img {
            width: 132px;
            position: relative;
            margin-top: 20px;
            margin-left: 17px;
        }
      }
    }
  }
  @media only screen and(max-width: 412px) {
    .login-form {
      height: 394px;
      width: 100%;
    }
  }
</style>

