<template>
<body>
  <div class="container">
    <nav>
      <div class="nav-toggle" v-on:click="navSide()">
        <i class="fas fa-bars"></i>
      </div>
      <div class="nav-brand">
        <img src="../assets/img/pac-logo.png" />
        <span>Web Admin PAC</span>
      </div>
      <div class="nav-account">
        <div class="info">
          <span>Welcome,</span>
          <b v-if="typeof data.userInfo !== 'undefined'">
            {{data.userInfo.userName}}
          </b>
        </div>
        <div class="logout" v-on:click="logout()">
          <div class="circle">
            <i class="fas fa-power-off"></i>
          </div>
          <span>Logout</span>
        </div>
      </div>
    </nav>
  </div>

  <div class="notif">
    <notifications group="home_notif" position="top center"/>
  </div>
  <main>
    <div class="container">
      <div class="row" style="padding-left: 8px;padding-right: 8px;">
        <div class="col-sm-12 col-md-3">
          <div class="sidebar" id="accordion">
            <div class="menu" @click="slideUp()">
              <router-link to="/dashboard" tag="li" active-class="active" class="li-tags">
                <div class="title">
                  <i class="fas fa-th-large"></i>
                  <p>
                    <!-- <router-link to="/dashboard">Dashboard</router-link> -->
                    Dashboard
                  </p>
                </div>
              </router-link>
            </div>

            <div class="menu">
              <div class="title">
                <i class="fas fa-user"></i>
                <p @click="toggle()">User and Role</p>
              </div>
              <ul class="dropdown">
                  <router-link to="/user" tag="li" active-class="active">User</router-link>
                  <router-link to="/role" tag="li" active-class="active">Role</router-link>
              </ul>
            </div>

            <div class="menu">
              <div class="title">
                <i class="fas fa-check-circle"></i>
                <p @click="toggle()">Approval</p>
              </div>
              <ul class="dropdown">
                  <router-link to="/activation-approval" tag="li" active-class="active">Activation Approval</router-link>
                  <router-link to="/customer-approval" tag="li" active-class="active">Manage Approval Customer</router-link>
                  <router-link to="/customer-deposito-approval" tag="li" active-class="active">Manage Approval Deposito</router-link>
                  <router-link to="/savings-account-approval" tag="li" active-class="active">Manage Approval Rekening</router-link>
              </ul>
            </div>

            <div class="menu" id="logout">
                <div class="title">
                   <i class="fas fa-power-off"></i>
                   <p v-on:click="logout()">Logout</p>
                </div>
            </div>
          </div>
        </div>
        <transition name="moveInUp">
          <router-view></router-view>
        </transition>
      </div>
    </div>
  </main>
  <footer>
    <p>Copyright {{ new Date().getFullYear() }} PT. Bank Pembangunan Daerah Nusa Tenggara Timur. All rights reserved.</p>
  </footer>
</body>
</template>

<script>
import 'select2/dist/js/select2'
import 'jquery/dist/jquery'
import 'jquery-ui/ui/jquery-1-7'
import 'slick-carousel/slick/slick'
import '../assets/js/script'
import '../assets/css/sanitize.css'
import '../assets/css/all.css'
import $ from 'jquery'
export default {
  name: "home",
  data() {
    return {
      data: {},
      loginError: false,
      error: false,
      errors: [],
      buttonLogout: false
    };
  },
  computed: {
    dataComputed() {
      return this.data;
    }
  },
  methods: {
    callLogout() {
      this.errors = [];
      this.$store
        .dispatch("logout")
        .then(() => {
          this.$router.push("/Login");
        })
        .catch(error => {
          this.loginError = true;
          this.errors.push(error);
          this.error = true;
        });
    },
    logout() {
      $('.logout').css('display','none');
      $('.menu#logout').css('display','none');
      this.$notify({
        group: 'notif',
        type:  'success',
        title: 'Logout Success',
        duration: 2000 
      });
      this.$store.dispatch("logout");
    },
    toggle() {
          $('.menu .title').click(function (e) {
            var current_dropdown = $(this).next(".dropdown");
            $(".dropdown").not(current_dropdown).slideUp();
            current_dropdown.slideDown();
            e.stopPropagation();
          });
          // if ($('.dropdown li').hasClass('active')) {
          //   $('.active').parent('.dropdown').toggle();
          //   $('.active').parent('.dropdown').siblings('.title').css('border-left', '4px solid #1487D6');
          //   $('.active').parent('.dropdown').siblings('.title').find('i, p').css('color', '#1487D6');
          // }
          
          // if ($('.menu .title p').hasClass('active')) {
          //   $('.active').parent('.title').css('border-left', '4px solid #1487D6');
          //   $('.active').css('color', '#1487D6');
          //   $('.active').siblings('i').css('color', '#1487D6');
          // }
    },
    slideUp() {
      $('.dropdown').slideUp('slow');
    },
    navSide() {
      $('.sidebar').toggle();
    }
  },
  created() {
    this.errors = [];
    // this.$store
    //   .dispatch("getProfile")
    //   .then(response => {
    //     this.data = response.data;
    //   })
    //   .catch(error => {
    //     this.loginError = true;
    //     this.errors.push(error);
    //     this.error = true;
    //   });
    this.$store.dispatch('SessionLogin')
        .then(result => {
          if (result == 'session_habis') {
            this.$notify({
              group: 'notif',
              title: 'Session Login Expired',
              text:  'Plese Login Again',
              type:  'warn',
              duration: 5000
            });
            this.$store.dispatch('logout');
          }
    });
  },
};
</script>

<style lang="scss" scoped>
body {
  background: #F5F5F5;
  padding-bottom: 50px;
  position: relative;
  margin: 0;
  min-height: 100vh;
}
.moveInUp-enter-active {
  animation: fadeIn 0.1s ease-in;
}

nav {
    margin-left: 0;
    margin-right: 0;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.moveInUp-leave-active {
  animation: moveInUp 0.1s ease-in;
}
@keyframes moveInUp {
  0% {
    transform: rotateX(0deg);
  }
  100% {
    transform: rotateX(-400px);
  }
}

// menu style
    .title {
        height: 43px;
    }
    .menu {
        .active {
            border-left: 4px solid rgb(20, 135, 214) !important;
            i, p {
              color: #1487d6 !important;
            }
            p {
                  margin-left: 13px;
            }
        }
        li {
          list-style-type: none;
        }
        .li-tags {
          .title {
            z-index: -15;
            position: relative;
          }
        }
        .dropdown {
          li {
              color: rgba(0, 0, 0, 0.6);
              cursor: pointer;
              display: block;
              font-size: 12px;
              font-weight: 500;
              text-decoration: none;
              padding: 6px 16px 6px 56px !important;            
          }
        }
    } 
    .menu#logout {
      display: none;    
    }
    @media only screen and(max-width: 990px) {
      nav {
        width: 100%;
        left: 0;
        margin: 0;
        border-radius: 0;
        .nav-toggle {
          i {
            color: white;
          }
        }
      }
      .menu#logout {
        display: block;
      }
    }
</style>