        login({ commit }, { user, password }) {
            return new Promise((resolve) => {
                $.post(serverUrl+'/authenticate', {
                    "username": user,
                    "password": password
                }).done(function(response) {
                    return resolve(response);
                }).fail(function() {
                    let status      =    { message: "No internet access" };
                    return resolve(status);
                });
                return commit;
            });
        },
        loginService({ commit }, { token }) {
            let datenow        =       moment.now();
            // let test_expired   =       '2019-12-21';
            const  expired     =       moment(datenow).format('YYYY-MM-DD');
            const  enkripData  =       keamanan.enkripsi(expired);
            localStorage.setItem("key_token", token);
            localStorage.setItem("expired", enkripData);
            commit('AuthToken', {
                token: token,
                expired: expired
            });
            setTimeout(() => {
                return router.replace('/');
            }, 2000);
        },
        logout() {
            localStorage.clear();
            setTimeout(() => {
                return router.replace('/login');    
            }, 2000);
        },
        SessionLogin () {
            return new Promise((resolve) => {
                let now              =   moment.now();
                let dateFormat       =   moment(now).format('YYYY-MM-DD');
                let localDate        =   localStorage.getItem('expired');
                let terjemaahDate    =   keamanan.terjemaah(localDate);
                // window.console.log(dateFormat);
                if (terjemaahDate >= dateFormat) {
                    let status       =   'next';
                    return resolve(status);
                } else {
                    let status       =   'session_habis';
                    return resolve(status);
                }
            });
        }
