import { AXIOS } from '../helper/http-commons'
import config from '../helper/auth-header'

export default {
    data() {
        var auth = null;
        if(localStorage.getItem('basic-auth') != null){
            auth = JSON.parse(localStorage.getItem('basic-auth'))
        }
        return {
            authHeader: auth
        }
    },
    createSession(user, password) {
        const auth = config.authHeader(user, password);
        
        this.data.authHeader = auth;
        return AXIOS.post('/authenticate', {
            username: user,
            password: password
        }, this.data.authHeader);


    },
    removeSession() {
        return AXIOS.delete('/authentication/session', this.data().authHeader );
    },
    getProfile() {
        return AXIOS.get('/authentication/my-profile', this.data().authHeader );
    }
}


