// import keamanan from './enkripsi'
export default {
    CekToken: function () { 
        try {
            let token       =     localStorage.getItem("key_token");
            if (token == null) {
                // window.location.href         =    '/login';
                return null;
            } else {
                // let token        =     keamanan.terjemaah(token);
                // return token;
                return token;
            }
            // return token;
        } catch (error) {
            window.console.log(error);
            return error;
        }
    }
}