import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Home from '@/components/Home'
import Dashboard from '@/components/Dashboard'
import ManageCustomer from '@/components/ManageCustomer'
import CustomerDepositoApproval from '@/components/CustomerDepositoApproval'
import SavingsAccountApproval from '@/components/SavingsAccountApproval'
import User from '@/components/User'
import Role from '@/components/Role'
import keamanan from './service/enkripsi'
// import store from './store'

Vue.use(Router);
const router = new Router({
    mode: 'history', // uris without hashes #, see https://router.vuejs.org/guide/essentials/history-mode.html#html5-history-mode
    routes: [
        { path: '/login', component: Login },
        {
            path: '/',
            component: Home,
            meta: {
                requiresAuth: true
            },
            children: [
                {
                    path: '/dashboard',
                    component: Dashboard,
                    meta: {
                        requiresAuth: true
                    }
                },
                {
                    path: '/user',
                    component: User,
                    meta: {
                        requiresAuth: true
                    }
                },
                {
                    path: '/role',
                    component: Role,
                    meta: {
                        requiresAuth: true
                    }
                },
                {
                    path: '/customer-approval',
                    component: ManageCustomer,
                    meta: {
                        requiresAuth: true
                    }
                },
                {
                    path: '/customer-deposito-approval',
                    component: CustomerDepositoApproval,
                    meta: {
                        requiresAuth: true
                    }
                },
                {
                    path: '/savings-account-approval',
                    component: SavingsAccountApproval,
                    meta: {
                        requiresAuth: true
                    }
                }
            ]
        },

        // otherwise redirect to home
        { path: '*', redirect: '/' }
    ]
});

router.beforeEach((to, from, next) => {
    const publicPages = ['/login'];
    const authRequired = !publicPages.includes(to.path);
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    // window.console.log(authRequired);
    // const session  = localStorage.getItem('session');
    let token      = localStorage.getItem("key_token");
    let dencrypted = keamanan.terjemaah(token);
    // window.console.log(dencrypted);
    if (authRequired && dencrypted == null) {
        return next({
            path: '/login',
            query: { returnUrl: to.path }
        });
    }
    next();
    // make sure to always call next()!
});

export default router;