<template>
  <div class="col-xs-12 col-sm-12 col-md-9">
    <div class="content">
      <header>
        <h2>Manage Role</h2>
      </header>
      <transition name="slide-fade">
        <div v-if="!details">
          <b-col sm="6" class="my-1">
            <b-form-group>
              <b-input-group size="sm">
                <b-form-input
                  type="search"
                  id="filterInput"
                  v-model="term"
                  placeholder="Type to Search"
                  v-on:keyup.enter="fetchData()"
                ></b-form-input>
                <b-input-group-append>
                  <b-button v-b-tooltip.hover @click="fetchData()" title="Cari">
                    <i class="fas fa-search"></i>
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
          </b-col>
          <div class="col-sm-12">
            <b-table
              show-empty
              :items="items"
              :fields="fields"
              :busy="isBusy"
              :current-page="currentPage"
              :per-page="0"
            >
              <template v-slot:table-busy>
                <div class="text-center text-danger my-2">
                  <b-spinner class="align-middle"></b-spinner>
                  <strong>Loading...</strong>
                </div>
              </template>
              <template v-slot:cell(actions)="row">
                <b-button
                  size="sm"
                  class="mr-1"
                  v-b-tooltip.hover
                  title="Edit"
                  @click="editData(row.item)"
                >
                  <i class="far fa-edit"></i>
                </b-button>
                <b-button
                  size="sm"
                  class="mr-1"
                  v-b-tooltip.hover
                  title="Remove"
                  @click="removeData(row.item)"
                >
                  <i class="far fa-trash-alt"></i>
                </b-button>
              </template>
            </b-table>
            <b-pagination
              align="center"
              v-model="currentPage"
              :total-rows="totalItems"
              :per-page="perPage"
              size="sm"
              aria-controls="my-table"
            ></b-pagination>
          </div>

          <div class="button-group">
            <a class="button button-next" @click="details = true">
              <i class="fas fa-plus"></i> Tambah Role
            </a>
          </div>
        </div>
      </transition>

      <transition name="details">
        <div v-if="details">
          <form @submit.prevent="savedData()">
            <div class="form-group">
              <div class="fieldset">
                <label>Nama Role</label>
                <b-form-input v-model="data.rolename"></b-form-input>
              </div>
              <div class="fieldset">
                <label>Deskripsi</label>
                <b-form-input v-model="data.description"></b-form-input>
              </div>
            </div>

            <hr />

            <div class="button-group">
              <button type="button" class="button-back" v-on:click="resetField()">Kembali</button>
              <button type="submit" class="button-next">Simpan</button>
            </div>
          </form>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
import roleService from "../service/role-service";
import { isNullOrUndefined } from "util";

export default {
  name: "user",
  data() {
    return {
      items: [],
      fields: [
        {
          key: "rolename",
          label: "Nama Role",
          sortable: true
        },
        {
          key: "description",
          label: "Deskripsi",
          sortable: true
        },
        { key: "actions", label: "Actions", class: "text-center" }
      ],
      currentPage: 1,
      perPage: 5,
      totalItems: 0,
      details: false,
      term: "",
      isBusy: false,
      data: {}
    };
  },
  mounted() {
    this.fetchData().catch(error => {
      window.console.error(error);
    });
  },
  methods: {
    async fetchData() {
      this.isBusy = true;
      return new Promise((resolve, reject) => {
        roleService
          .getRole(this.currentPage, this.perPage, this.term)
          .then(response => {
            this.totalItems = response.data.count;
            this.items = response.data.data;
            this.isBusy = false;
            resolve(response);
          })
          .catch(() => {
            reject("error");
          });
      });
    },
    savedData() {
      return new Promise((resolve, reject) => {
        roleService
          .inserOrUpdateRole(this.data)
          .then(response => {
            this.$notify({
              group: "notif",
              type: "success",
              title: "Info: ",
              text: response.data.message
            });
            this.resetField();
            resolve(response);
          })
          .catch(err => {
            reject(err);
          });
      });
    },
    editData(data) {
      this.data = data;
      this.details = true;
    },
    removeData(data) {
      if (
        confirm("Apakah anda ingin menghapus role '" + data.rolename + "' ?")
      ) {
        return new Promise((resolve, reject) => {
          roleService
            .removeRole(data)
            .then(response => {
              this.$notify({
                group: "notif",
                type: "success",
                title: "Info: ",
                text: response.data.message
              });
              this.fetchData().catch(() => {});
              resolve(response);
            })
            .catch(err => {
              reject(err);
            });
        });
      }
    },
    resetField() {
      this.fetchData();
      this.data = {};
      this.details = !this.details;
    }
  },
  watch: {
    currentPage: {
      handler: function(value) {
        if (!isNullOrUndefined(value)) {
          this.fetchData().catch(() => {});
        }
      }
    }
  },
  created() {
    this.$store.dispatch('SessionLogin')
        .then(result => {
          if (result == 'session_habis') {
            this.$notify({
              group: 'notif',
              title: 'Session Login Expired',
              text:  'Plese Login Again',
              type:  'warn',
              duration: 5000
            });
            this.$store.dispatch('logout');
          }
    });
  }
};
</script>
<style>
.slide-fade-enter-active {
  transition: all 0.1s ease;
}
.slide-fade-leave-active {
  transition: all 0.1s ease;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>