import { AXIOS } from '../helper/http-commons'
import auth from './authenticated-service'
import { isNullOrUndefined } from 'util';

export default {
    getUser( offset, maxResult, term) {
        let config = {}
        config = auth.data().authHeader;
        config.params = {
                offset : offset,
                maxResult : maxResult,
                term : term
        }
        return AXIOS.get('/user/', config);
    },
    createUser(data) {
        if(isNullOrUndefined(data.userId)){
            return AXIOS.post('/user/', data, auth.data().authHeader);
        }else{
            return AXIOS.put('/user/', data, auth.data().authHeader);
        }
        
    },
    removeUser(data){
        if(!isNullOrUndefined(data.userId)){
            let config = {}
            config = auth.data().authHeader;
            config.params = {
                    id : data.userId
            }
            return AXIOS.delete('/user/', config);
        }
    }
}


